// style go here
.Carousel {
  margin-inline: auto;

  &__title {
    text-align: center;
  }

  &__inputs {
    margin: 10px;

    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }

  &__list-wrapper {
    margin: 0 auto;
    height: 130px;

    position: relative;

    border: 3px solid black;
    border-radius: 15px;
    overflow: hidden;
  }

  &__list {
    display: flex;

    padding: 0;
    margin: 0;

    position: absolute;
  }

  &__list-img {
    display: flex;
  }

  &__button-wrapper {
    margin-top: 20px;

    display: flex;
    justify-content: center;
    gap: 30px;
  }

  &__button {
    width: 100px;
    border-radius: 5px;
    cursor: pointer;
  }
}

.input {
  width: 50px;
}
